.paginateContainer{
  display: flex;
  align-items: center;
  justify-content: center;
}

.item__paginate {
  background-color: #c9c8c8;
  padding: 0.5em 1em;
  border: 2px solid #f7f4f4;
  border-radius: 4px;
  cursor: pointer;
  font-weight: 700;
}

.activePage, .item__paginate:hover {
  background-color: #999;
}

